import { Platform, Alert } from 'react-native';
import deviceInfoManager from './deviceInfo';
import { performanceMonitor } from './performanceMonitor';
import permissionManager from './permissions';
import nativeModulesManager from './nativeModules';
import notificationManager from './notifications';

export interface TestResult {
  testName: string;
  passed: boolean;
  duration: number;
  error?: string;
  details?: any;
}

export interface TestSuite {
  name: string;
  tests: TestResult[];
  passed: boolean;
  totalDuration: number;
  passRate: number;
}

export interface IntegrationTestReport {
  deviceInfo: any;
  testSuites: TestSuite[];
  overallResult: {
    totalTests: number;
    passedTests: number;
    failedTests: number;
    passRate: number;
    totalDuration: number;
  };
  performanceMetrics: any;
  recommendations: string[];
}

class DeviceIntegrationTester {
  private testResults: TestResult[] = [];
  private currentSuite: string = '';

  /**
   * ËøêË°åÂÆåÊï¥ÁöÑÈõÜÊàêÊµãËØï
   */
  async runFullIntegrationTest(): Promise<IntegrationTestReport> {
    console.log('üß™ ÂºÄÂßãÁúüÂÆûËÆæÂ§áÈõÜÊàêÊµãËØï...');
    
    // ÂºÄÂßãÊÄßËÉΩÁõëÊéß
    performanceMonitor.startMonitoring(1000);
    
    const startTime = Date.now();
    const testSuites: TestSuite[] = [];

    try {
      // Ëé∑ÂèñËÆæÂ§á‰ø°ÊÅØ
      const deviceInfo = await deviceInfoManager.getDeviceSpecs();
      console.log('üì± ËÆæÂ§á‰ø°ÊÅØËé∑ÂèñÂÆåÊàê');

      // ËøêË°åÂêÑ‰∏™ÊµãËØïÂ•ó‰ª∂
      testSuites.push(await this.runDeviceCompatibilityTests());
      testSuites.push(await this.runPermissionTests());
      testSuites.push(await this.runNativeModuleTests());
      testSuites.push(await this.runNotificationTests());
      testSuites.push(await this.runPerformanceTests());
      testSuites.push(await this.runNetworkTests());

      // ËÆ°ÁÆóÊÄª‰ΩìÁªìÊûú
      const totalTests = testSuites.reduce((sum, suite) => sum + suite.tests.length, 0);
      const passedTests = testSuites.reduce((sum, suite) => sum + suite.tests.filter(t => t.passed).length, 0);
      const failedTests = totalTests - passedTests;
      const totalDuration = Date.now() - startTime;

      // Ëé∑ÂèñÊÄßËÉΩÊåáÊ†á
      const performanceMetrics = performanceMonitor.getPerformanceReport();
      
      // ÁîüÊàêÂª∫ËÆÆ
      const recommendations = this.generateRecommendations(testSuites, performanceMetrics);

      const report: IntegrationTestReport = {
        deviceInfo,
        testSuites,
        overallResult: {
          totalTests,
          passedTests,
          failedTests,
          passRate: (passedTests / totalTests) * 100,
          totalDuration,
        },
        performanceMetrics,
        recommendations,
      };

      console.log(`‚úÖ ÈõÜÊàêÊµãËØïÂÆåÊàê: ${passedTests}/${totalTests} ÈÄöËøá (${report.overallResult.passRate.toFixed(1)}%)`);
      return report;

    } catch (error) {
      console.error('‚ùå ÈõÜÊàêÊµãËØïÂ§±Ë¥•:', error);
      throw error;
    } finally {
      performanceMonitor.stopMonitoring();
    }
  }

  /**
   * ËÆæÂ§áÂÖºÂÆπÊÄßÊµãËØï
   */
  private async runDeviceCompatibilityTests(): Promise<TestSuite> {
    this.currentSuite = 'ËÆæÂ§áÂÖºÂÆπÊÄßÊµãËØï';
    console.log(`üîç ÂºÄÂßã${this.currentSuite}...`);
    
    const tests: TestResult[] = [];
    const startTime = Date.now();

    // ÊµãËØïËÆæÂ§á‰ø°ÊÅØËé∑Âèñ
    tests.push(await this.runTest('Ëé∑ÂèñËÆæÂ§á‰ø°ÊÅØ', async () => {
      const deviceSpecs = await deviceInfoManager.getDeviceSpecs();
      if (!deviceSpecs.deviceId || !deviceSpecs.brand || !deviceSpecs.model) {
        throw new Error('ËÆæÂ§á‰ø°ÊÅØ‰∏çÂÆåÊï¥');
      }
      return deviceSpecs;
    }));

    // ÊµãËØïÂÖºÂÆπÊÄßÊ£ÄÊü•
    tests.push(await this.runTest('ÂÖºÂÆπÊÄßÊ£ÄÊü•', async () => {
      const compatibility = await deviceInfoManager.checkCompatibility();
      return compatibility;
    }));

    // ÊµãËØïÁ≥ªÁªüÁâàÊú¨
    tests.push(await this.runTest('Á≥ªÁªüÁâàÊú¨Ê£ÄÊü•', async () => {
      const deviceSpecs = await deviceInfoManager.getDeviceSpecs();
      if (Platform.OS === 'ios') {
        const version = parseFloat(deviceSpecs.systemVersion);
        if (version < 12.0) {
          throw new Error(`iOSÁâàÊú¨Ëøá‰Ωé: ${version}`);
        }
      } else if (Platform.OS === 'android') {
        const version = parseInt(deviceSpecs.systemVersion);
        if (version < 21) {
          throw new Error(`AndroidÁâàÊú¨Ëøá‰Ωé: ${version}`);
        }
      }
      return { version: deviceSpecs.systemVersion, compatible: true };
    }));

    // ÊµãËØïÂÜÖÂ≠òÊ£ÄÊü•
    tests.push(await this.runTest('ÂÜÖÂ≠òÊ£ÄÊü•', async () => {
      const deviceSpecs = await deviceInfoManager.getDeviceSpecs();
      const memoryGB = deviceSpecs.totalMemory / (1024 * 1024 * 1024);
      if (memoryGB < 1) {
        throw new Error(`ÂÜÖÂ≠ò‰∏çË∂≥: ${memoryGB.toFixed(2)}GB`);
      }
      return { totalMemory: memoryGB, sufficient: true };
    }));

    const totalDuration = Date.now() - startTime;
    const passedTests = tests.filter(t => t.passed).length;

    return {
      name: this.currentSuite,
      tests,
      passed: passedTests === tests.length,
      totalDuration,
      passRate: (passedTests / tests.length) * 100,
    };
  }

  /**
   * ÊùÉÈôêÊµãËØï
   */
  private async runPermissionTests(): Promise<TestSuite> {
    this.currentSuite = 'ÊùÉÈôêÁ≥ªÁªüÊµãËØï';
    console.log(`üîç ÂºÄÂßã${this.currentSuite}...`);
    
    const tests: TestResult[] = [];
    const startTime = Date.now();

    // ÊµãËØïÊùÉÈôêÊ£ÄÊü•
    tests.push(await this.runTest('Áõ∏Êú∫ÊùÉÈôêÊ£ÄÊü•', async () => {
      const result = await permissionManager.checkPermission('camera');
      return result;
    }));

    tests.push(await this.runTest('È∫¶ÂÖãÈ£éÊùÉÈôêÊ£ÄÊü•', async () => {
      const result = await permissionManager.checkPermission('microphone');
      return result;
    }));

    tests.push(await this.runTest('‰ΩçÁΩÆÊùÉÈôêÊ£ÄÊü•', async () => {
      const result = await permissionManager.checkPermission('location');
      return result;
    }));

    // ÊµãËØïÂÅ•Â∫∑Â∫îÁî®ÊùÉÈôêÁªÑÂêà
    tests.push(await this.runTest('ÂÅ•Â∫∑Â∫îÁî®ÊùÉÈôêÊ£ÄÊü•', async () => {
      const result = await permissionManager.checkHealthAppPermissions();
      return result;
    }));

    const totalDuration = Date.now() - startTime;
    const passedTests = tests.filter(t => t.passed).length;

    return {
      name: this.currentSuite,
      tests,
      passed: passedTests === tests.length,
      totalDuration,
      passRate: (passedTests / tests.length) * 100,
    };
  }

  /**
   * ÂéüÁîüÊ®°ÂùóÊµãËØï
   */
  private async runNativeModuleTests(): Promise<TestSuite> {
    this.currentSuite = 'ÂéüÁîüÊ®°ÂùóÊµãËØï';
    console.log(`üîç ÂºÄÂßã${this.currentSuite}...`);
    
    const tests: TestResult[] = [];
    const startTime = Date.now();

    // ÊµãËØïÊ®°ÂùóÁä∂ÊÄÅ
    tests.push(await this.runTest('Ê®°ÂùóÁä∂ÊÄÅÊ£ÄÊü•', async () => {
      const status = nativeModulesManager.getModulesStatus();
      return status;
    }));

    // ÊµãËØïÁõ∏Êú∫ÂèØÁî®ÊÄß
    tests.push(await this.runTest('Áõ∏Êú∫ÂèØÁî®ÊÄßÊµãËØï', async () => {
      const available = await nativeModulesManager.isCameraAvailable();
      return { available };
    }));

    // ÊµãËØïÂÅ•Â∫∑ÂäüËÉΩÂàùÂßãÂåñ
    tests.push(await this.runTest('ÂÅ•Â∫∑ÂäüËÉΩÂàùÂßãÂåñ', async () => {
      const result = await nativeModulesManager.initializeHealthFeatures();
      return result;
    }));

    // ÊµãËØï‰ΩçÁΩÆÊúçÂä°
    tests.push(await this.runTest('‰ΩçÁΩÆÊúçÂä°ÊµãËØï', async () => {
      try {
        const location = await nativeModulesManager.getCurrentLocation({
          timeout: 10000,
          enableHighAccuracy: false,
        });
        return location;
             } catch (error: any) {
         // ‰ΩçÁΩÆÊúçÂä°ÂèØËÉΩÂõ†‰∏∫ÊùÉÈôêÊàñÁΩëÁªúÈóÆÈ¢òÂ§±Ë¥•ÔºåËøôÊòØÂèØÊé•ÂèóÁöÑ
         return { error: (error instanceof Error ? error.message : String(error)), skipped: true };
       }
    }));

    const totalDuration = Date.now() - startTime;
    const passedTests = tests.filter(t => t.passed).length;

    return {
      name: this.currentSuite,
      tests,
      passed: passedTests === tests.length,
      totalDuration,
      passRate: (passedTests / tests.length) * 100,
    };
  }

  /**
   * ÈÄöÁü•Á≥ªÁªüÊµãËØï
   */
  private async runNotificationTests(): Promise<TestSuite> {
    this.currentSuite = 'ÈÄöÁü•Á≥ªÁªüÊµãËØï';
    console.log(`üîç ÂºÄÂßã${this.currentSuite}...`);
    
    const tests: TestResult[] = [];
    const startTime = Date.now();

    // ÊµãËØïÈÄöÁü•Áä∂ÊÄÅ
    tests.push(await this.runTest('ÈÄöÁü•Á≥ªÁªüÁä∂ÊÄÅ', async () => {
      const status = notificationManager.getNotificationStatus();
      return status;
    }));

    // ÊµãËØïÊùÉÈôêÊ£ÄÊü•
    tests.push(await this.runTest('ÈÄöÁü•ÊùÉÈôêÊ£ÄÊü•', async () => {
      const hasPermission = await notificationManager.checkNotificationPermission();
      return { hasPermission };
    }));

    // ÊµãËØïÊú¨Âú∞ÈÄöÁü•
    tests.push(await this.runTest('Êú¨Âú∞ÈÄöÁü•ÊµãËØï', async () => {
      const success = await notificationManager.scheduleLocalNotification({
        id: 'test_notification',
        title: 'ÊµãËØïÈÄöÁü•',
        body: 'ËøôÊòØ‰∏Ä‰∏™ÊµãËØïÈÄöÁü•',
        date: new Date(Date.now() + 5000), // 5ÁßíÂêé
      });
      
      // Á´ãÂç≥ÂèñÊ∂àÊµãËØïÈÄöÁü•
      notificationManager.cancelLocalNotification('test_notification');
      
      return { success };
    }));

    const totalDuration = Date.now() - startTime;
    const passedTests = tests.filter(t => t.passed).length;

    return {
      name: this.currentSuite,
      tests,
      passed: passedTests === tests.length,
      totalDuration,
      passRate: (passedTests / tests.length) * 100,
    };
  }

  /**
   * ÊÄßËÉΩÊµãËØï
   */
  private async runPerformanceTests(): Promise<TestSuite> {
    this.currentSuite = 'ÊÄßËÉΩÊµãËØï';
    console.log(`üîç ÂºÄÂßã${this.currentSuite}...`);
    
    const tests: TestResult[] = [];
    const startTime = Date.now();

    // ÊµãËØïÂêØÂä®ÊÄßËÉΩ
    tests.push(await this.runTest('Â∫îÁî®ÂêØÂä®ÊÄßËÉΩ', async () => {
      const startupMetrics = await performanceMonitor.testAppStartupPerformance();
      if (startupMetrics.firstRender > 3000) {
        throw new Error(`È¶ñÊ¨°Ê∏≤ÊüìÊó∂Èó¥ËøáÈïø: ${startupMetrics.firstRender}ms`);
      }
      return startupMetrics;
    }));

    // ÊµãËØïÂÜÖÂ≠òÊ≥ÑÊºè
    tests.push(await this.runTest('ÂÜÖÂ≠òÊ≥ÑÊºèÊµãËØï', async () => {
      const memoryTest = await performanceMonitor.testMemoryLeaks(5);
      if (memoryTest.averageGrowthPerIteration > 10 * 1024 * 1024) { // 10MB per iteration
        throw new Error(`ÂÜÖÂ≠òÂ¢ûÈïøËøáÂø´: ${memoryTest.averageGrowthPerIteration / (1024 * 1024)}MB per iteration`);
      }
      return memoryTest;
    }));

    // ÊµãËØïÂΩìÂâçÊÄßËÉΩÊåáÊ†á
    tests.push(await this.runTest('ÂΩìÂâçÊÄßËÉΩÊåáÊ†á', async () => {
      const metrics = await deviceInfoManager.getCurrentPerformanceMetrics();
      if (metrics.memoryUsage.percentage > 90) {
        throw new Error(`ÂÜÖÂ≠ò‰ΩøÁî®ÁéáËøáÈ´ò: ${metrics.memoryUsage.percentage.toFixed(1)}%`);
      }
      return metrics;
    }));

    const totalDuration = Date.now() - startTime;
    const passedTests = tests.filter(t => t.passed).length;

    return {
      name: this.currentSuite,
      tests,
      passed: passedTests === tests.length,
      totalDuration,
      passRate: (passedTests / tests.length) * 100,
    };
  }

  /**
   * ÁΩëÁªúÊµãËØï
   */
  private async runNetworkTests(): Promise<TestSuite> {
    this.currentSuite = 'ÁΩëÁªúËøûÊé•ÊµãËØï';
    console.log(`üîç ÂºÄÂßã${this.currentSuite}...`);
    
    const tests: TestResult[] = [];
    const startTime = Date.now();

    // ÊµãËØïÁΩëÁªúËøûÊé•
    tests.push(await this.runTest('ÁΩëÁªúËøûÊé•ÊµãËØï', async () => {
      const startTime = Date.now();
             const response = await fetch('https://www.google.com', { 
         method: 'HEAD',
       });
      const latency = Date.now() - startTime;
      
      if (!response.ok) {
        throw new Error(`ÁΩëÁªúËØ∑Ê±ÇÂ§±Ë¥•: ${response.status}`);
      }
      
      if (latency > 5000) {
        throw new Error(`ÁΩëÁªúÂª∂ËøüËøáÈ´ò: ${latency}ms`);
      }
      
      return { latency, status: response.status };
    }));

    // ÊµãËØïAPIËøûÊé•
    tests.push(await this.runTest('APIËøûÊé•ÊµãËØï', async () => {
      try {
        // ËøôÈáåÂèØ‰ª•ÊµãËØïÂÆûÈôÖÁöÑAPIÁ´ØÁÇπ
        const startTime = Date.now();
        const response = await fetch('https://httpbin.org/get', {
          method: 'GET',
          timeout: 10000,
        });
        const latency = Date.now() - startTime;
        
        if (!response.ok) {
          throw new Error(`APIËØ∑Ê±ÇÂ§±Ë¥•: ${response.status}`);
        }
        
        return { latency, status: response.status };
      } catch (error) {
        // APIÊµãËØïÂ§±Ë¥•ÊòØÂèØÊé•ÂèóÁöÑÔºåÂèØËÉΩÊòØÁΩëÁªúÈóÆÈ¢ò
        return { error: (error instanceof Error ? error.message : String(error)), skipped: true };
      }
    }));

    const totalDuration = Date.now() - startTime;
    const passedTests = tests.filter(t => t.passed).length;

    return {
      name: this.currentSuite,
      tests,
      passed: passedTests === tests.length,
      totalDuration,
      passRate: (passedTests / tests.length) * 100,
    };
  }

  /**
   * ËøêË°åÂçï‰∏™ÊµãËØï
   */
  private async runTest(testName: string, testFunction: () => Promise<any>): Promise<TestResult> {
    const startTime = Date.now();
    
    try {
      console.log(`  üß™ ËøêË°åÊµãËØï: ${testName}`);
      const result = await testFunction();
      const duration = Date.now() - startTime;
      
      console.log(`  ‚úÖ ${testName} - ${duration}ms`);
      return {
        testName,
        passed: true,
        duration,
        details: result,
      };
    } catch (error) {
      const duration = Date.now() - startTime;
      console.log(`  ‚ùå ${testName} - ${duration}ms - ${(error instanceof Error ? error.message : String(error))}`);
      
      return {
        testName,
        passed: false,
        duration,
        error: (error instanceof Error ? error.message : String(error)),
      };
    }
  }

  /**
   * ÁîüÊàê‰ºòÂåñÂª∫ËÆÆ
   */
  private generateRecommendations(testSuites: TestSuite[], performanceMetrics: any): string[] {
    const recommendations: string[] = [];

    // Âü∫‰∫éÊµãËØïÁªìÊûúÁîüÊàêÂª∫ËÆÆ
    testSuites.forEach(suite => {
      if (suite.passRate < 100) {
        recommendations.push(`${suite.name}Â≠òÂú®ÈóÆÈ¢òÔºåÂª∫ËÆÆÊ£ÄÊü•Â§±Ë¥•ÁöÑÊµãËØïÈ°π`);
      }
    });

    // Âü∫‰∫éÊÄßËÉΩÊåáÊ†áÁîüÊàêÂª∫ËÆÆ
    if (performanceMetrics.summary.criticalAlerts > 0) {
      recommendations.push('Ê£ÄÊµãÂà∞‰∏•ÈáçÊÄßËÉΩÈóÆÈ¢òÔºåÂª∫ËÆÆÁ´ãÂç≥‰ºòÂåñ');
    }

    if (performanceMetrics.summary.memoryIssues > 0) {
      recommendations.push('ÂÜÖÂ≠ò‰ΩøÁî®Â≠òÂú®ÈóÆÈ¢òÔºåÂª∫ËÆÆ‰ºòÂåñÂÜÖÂ≠òÁÆ°ÁêÜ');
    }

    // ÈÄöÁî®Âª∫ËÆÆ
    recommendations.push('ÂÆöÊúüËøêË°åÈõÜÊàêÊµãËØï‰ª•Á°Æ‰øùÂ∫îÁî®Á®≥ÂÆöÊÄß');
    recommendations.push('Âú®‰∏çÂêåËÆæÂ§á‰∏äÊµãËØï‰ª•Á°Æ‰øùÂÖºÂÆπÊÄß');
    recommendations.push('ÁõëÊéßÂ∫îÁî®ÊÄßËÉΩÂπ∂ÂèäÊó∂‰ºòÂåñ');

    return recommendations;
  }

  /**
   * ÁîüÊàêÊµãËØïÊä•Âëä
   */
  generateTestReport(report: IntegrationTestReport): string {
    const { deviceInfo, testSuites, overallResult, recommendations } = report;

    let reportText = `
# Á¥¢ÂÖãÁîüÊ¥ªÁúüÂÆûËÆæÂ§áÈõÜÊàêÊµãËØïÊä•Âëä

## ËÆæÂ§á‰ø°ÊÅØ
- ÂìÅÁâå: ${deviceInfo.brand}
- ÂûãÂè∑: ${deviceInfo.model}
- Á≥ªÁªü: ${deviceInfo.systemName} ${deviceInfo.systemVersion}
- ÂÜÖÂ≠ò: ${(deviceInfo.totalMemory / (1024 * 1024 * 1024)).toFixed(2)}GB
- ÊòØÂê¶Ê®°ÊãüÂô®: ${deviceInfo.isEmulator ? 'ÊòØ' : 'Âê¶'}

## ÊµãËØïÊÄªËßà
- ÊÄªÊµãËØïÊï∞: ${overallResult.totalTests}
- ÈÄöËøáÊµãËØï: ${overallResult.passedTests}
- Â§±Ë¥•ÊµãËØï: ${overallResult.failedTests}
- ÈÄöËøáÁéá: ${overallResult.passRate.toFixed(1)}%
- ÊÄªËÄóÊó∂: ${overallResult.totalDuration}ms

## ÊµãËØïÂ•ó‰ª∂ËØ¶ÊÉÖ
`;

    testSuites.forEach(suite => {
      reportText += `
### ${suite.name}
- ÈÄöËøáÁéá: ${suite.passRate.toFixed(1)}%
- ËÄóÊó∂: ${suite.totalDuration}ms
- Áä∂ÊÄÅ: ${suite.passed ? '‚úÖ ÈÄöËøá' : '‚ùå Â§±Ë¥•'}

#### ÊµãËØïËØ¶ÊÉÖ:
`;
      suite.tests.forEach(test => {
        reportText += `- ${test.passed ? '‚úÖ' : '‚ùå'} ${test.testName} (${test.duration}ms)`;
        if (test.error) {
          reportText += ` - ÈîôËØØ: ${test.error}`;
        }
        reportText += '\n';
      });
    });

    reportText += `
## ‰ºòÂåñÂª∫ËÆÆ
${recommendations.map(rec => `- ${rec}`).join('\n')}

---
Êä•ÂëäÁîüÊàêÊó∂Èó¥: ${new Date().toLocaleString()}
    `;

    return reportText.trim();
  }
}

export const deviceIntegrationTester = new DeviceIntegrationTester();
export default deviceIntegrationTester; 